<template>
<div class="login">
    <div class="box">
        <h3 class="center marginBottom">登录</h3>
      <div class="marginBottom">
        <el-input placeholder="请输入用户名" v-model="form.username"></el-input>
      </div>
      <div class="marginBottom">
        <el-input placeholder="请输入密码" v-model="form.password" @keyup.enter.native="login"></el-input>
      </div>
      <div class="center">
        <el-button type="primary" @click="login">登录</el-button>
      </div>
    </div>
</div>
</template>
<script>
 import {mapGetters,mapActions} from 'vuex';
 import {userlogin} from '../../utils/request'
 import {alertSuccess,alertWarn} from '../../utils/alert'
export default {
props: [],
components: {},
data() {
return {
  form:{
    username:"admin",
    password:"123"
  }
};
},
computed: {
...mapGetters({}),
},
methods: {
...mapActions({
  reqchangeinfo:"reqchangeinfo"
}), 
login(){
  userlogin(this.form).then(res=>{
    if(res.data.code==200){
      alertSuccess(res.data.msg)
      this.reqchangeinfo(res.data.list)
      this.$router.push('/index')
    }else{
      alertWarn(res.data.msg)
    }
  })
}
},
mounted() {},
};
</script>
<style scoped>
.login{
    width: 100vw;
    height: 100vh;
    position: relative;
    background: linear-gradient(to right,rgb(86,52,67),rgb(47,61,96));
}
h3{
    font-weight: bolder;
    font-size: 20px;
    margin: 20px 0;
}
.box{
  width: 400px;
  padding: 20px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  background: white;
  border-radius: 10px;
}
.center{
  text-align: center;
}
.marginBottom{
  margin-bottom: 20px;
}
</style>


